import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { PropertyWorthState } from '@core/data-access/states/property-worth.state';

@Component({
	selector: 'findqo-place-free-ad-banner',
	templateUrl: './place-free-ad-banner.component.html',
	styleUrls: ['./place-free-ad-banner.component.scss'],
	standalone: true,
})
export class PlaceFreeAdBannerComponent {
	constructor(
		private router: Router,
		private propertyWorthState: PropertyWorthState,
	) {}
	@Input() valuationData: any;
	onPlaceAd() {
		let aisle = 'sell-residential';
		if (this.valuationData?.aisle) {
			aisle = this.valuationData.aisle;
		}

		let section = 'properties-for-sale';
		if (aisle === 'rent-residential' || aisle === 'share-residential') {
			section = 'properties-for-rent';
		}
		this.propertyWorthState.setValuationInfo({ ...this.valuationData, section, aisle });
		this.router.navigate([APP_ROUTE.placeAdCategory, section, aisle]);
	}
}
